<template>
    <div class="content">
      <el-form ref="form" :model="formData" label-width="80px" class="login">
      <el-form-item label="账号" class="userName">
        <el-input v-model="formData.user"></el-input>
      </el-form-item>
        <el-form-item label="密码">
        <el-input type="password" v-model="formData.pwd"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit">登录</el-button>
      </el-form-item>
      </el-form>
    </div>
</template>

<script>
    export default {
      name: "login",
      data(){
          return{
            formData:{
              user: 'admin',
              pwd: '11111'
            }
          }
      },
      methods:{
        submit(){
          this.$axios.post('login',this.formData).then(res=>{
            if(res.code==200){
              this.$message({
                type: 'success',
                message: '登录成功!'
              });
              setTimeout(()=>{
                this.$router.push("/header")
              },500)
            }else{
              this.$message({
                type: 'warning',
                message: res.msg
              });
            }

          })
        }
      }
    }
</script>
<style>
  .login .el-input__inner{
    background: rgba(0,0,0,0);
    color: #fff;
  }
  .login .el-form-item__label{
    color: #fff;
  }
</style>

<style scoped>
.content{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: url("../../assets/header.jpg") no-repeat;
  background-size: 100% 100%;
}
  .login{
    width: 500px;
    margin: 150px auto;
  }
</style>



